import React from 'react';
import PropTypes from 'prop-types';
import './AddCity.css';

const AddCity = ({ value, addCity, onChange }) => (
  <div className="add-city">
    <span className="add-city__header">
      Lgg till stad
    </span>
    <div className="add-city-input">
      <div className="add-city-input-container">
        <input type="text" className="add-city-input-field__input" onChange={event => onChange(event)} value={value} />
      </div>
      <button type="button" onClick={addCity}>
        <i className="fas fa-2x fa-plus-circle" />
      </button>
    </div>
  </div>
);

AddCity.defaultProps = {
  value: '',
};

AddCity.propTypes = {
  addCity: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.string,
};

export default AddCity;



