import React from 'react';
import PropTypes from 'prop-types';
import './CityListItem.css';
import WeatherIconSelector from './WeatherIconSelector';

const CityListItem = ({ item, removeItem }) => (
  <a href={`city/${item.id}`} className="city-list-item">
    <span className="city-list-item__text">
      {item.name}
    </span>
    <div className="city-list-item--flex">
      <span className="city-list-item__text">
        {Math.round(item.main.temp)}
      </span>
      <WeatherIconSelector type={item.weather[0].main} />
      <button type="button" className="city-list-item__remove-button" onClick={e => removeItem(item, e)}>
        <i className="fa fa-2x fa-minus-circle" />
      </button>
    </div>
  </a>
);

CityListItem.propTypes = {
  item: PropTypes.shape({ name: PropTypes.string }).isRequired,
  removeItem: PropTypes.func.isRequired,
};

export default CityListItem;



